.burger {
  display: none;
  position: relative;
  width: 40px;
  height: 40px;

  @include mobile {
    display: block;
  }

  &__line {
    position: absolute;
    left: 50%;
    top: 50%;
    width: 26px;
    height: 2px;
    background-color: var(--light-color);
    transform: translateX(-50%) translateY(-50%);

    &:first-child {
      margin-top: -7px;
    }

    &:last-child {
      margin-top: 7px;
    }
  }
}

.burger-close {
  display: none;
  position: absolute;
  right: 16px;
  top: 20px;
  width: 40px;
  height: 40px;
  background-image: url('../img/close.svg');
  background-position: center;
  background-size: 20px 20px;
  background-repeat: no-repeat;

  @include mobile {
    display: block;
  }
}
