.product {
  padding-top: 23px;
  background-color: var(--dark-color);

  &__container {
    display: flex;

    @include high-tablet {
      flex-direction: column;
    }
  }

  &__list {
    display: flex;
    margin-bottom: 7px;

    @include high-tablet {
      margin-bottom: 24px;
    }
  }

  &__item {
    letter-spacing: -0.8px;

    &:not(:last-child) {
      margin-right: 22px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 16px;
    color: var(--text-color);
    position: relative;

    @include mobile {
      font-size: 14px;
    }

    &--active {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      color: var(--light-color);
    }

    &::after {
      content: "/";
      position: absolute;
      right: -10px;
      top: 2px;
    }
  }

  &__images {
    width: 50%;

    @include high-tablet {
      width: 100%;
    }
  }

  &__descr {
    width: 50%;
    padding: 26px 12px 0 27px;

    @include high-tablet {
      margin-top: 40px;
      padding: 0;
      width: 100%;
    }
  }

  &__versions {
    @include flex-all-sb;

    margin-bottom: 24px;

    @include high-tablet {
      flex-direction: row-reverse;
    }

    @include mobile {
      margin-bottom: 22px;
    }
  }

  &__brand, &__txt {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: var(--text-color);

    @include mobile {
      margin-bottom: 9px;
    }
  }

  &__txt {
    margin-bottom: 40px;
  }

  &__name {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 700;
    font-size: 46px;
    line-height: 58px;
    color: var(--light-color);

    @include high-desktop {
      font-size: 36px;
    }

    @include small-tablet {
      font-size: 28px;
    }

    @include mobile {
      font-weight: 400;
      font-size: 18px;
      letter-spacing: 0.3px;
      max-width: 250px;
      line-height: 22px;
    }
  }

  &__properties {
    display: flex;
    justify-content: space-between;

    @include high-desktop {
      flex-direction: column;
    }
  }
}

.rating {
  margin-bottom: 24px;
  display: inline-flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  width: 216px;
  height: 40px;
  background: var(--highlight-color);
  border-radius: 8px;

  @include mobile {
    margin-bottom: 26px;
    width: 180px;
    height: 34px;
  }

  &__number {
    margin-right: 10px;
    font-weight: 400;
    font-size: 24px;
    line-height: 30px;
    color: var(--light-color);

    @include mobile {
      margin-right: 8px;
      font-size: 20px;
    }
  }

  &__item {
    font-size: 20px;
    color: var(--inactive-color);
    transition: all .3s ease-in-out;

    &:not(:first-child) {
      margin-right: 10px;

      @include mobile {
        margin-right: 3px;
      }
    }
  }

  &[data-total-value="1"] &__item:nth-child(n + 5),
  &[data-total-value="2"] &__item:nth-child(n + 4),
  &[data-total-value="3"] &__item:nth-child(n + 3),
  &[data-total-value="4"] &__item:nth-child(n + 2),
  &[data-total-value="5"] &__item:nth-child(n + 1) {
    color: var(--accent-color);
  }
}

.version {
  &__list {
    display: flex;
  }

  &__item {

    &:not(:last-child) {
      margin-right: 16px;

      @include mobile {
        margin-right: 8px;
      }
    }

  }

  &__btn {
    width: 90px;
    height: 26px;
    border: 1px solid var(--element-color);
    border-radius: 10px;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: var(--text-color);
    transition: all .3s ease-in-out;

    @include mobile {
      width: 69px;
    }

    &--active {
      background-color: var(--accent-color);
      border: 1px solid var(--accent-color);
      color: var(--light-color);
    }

    &--new {
      width: 55px;
      height: 30px;
      background: var(--highlight-color);
      border-radius: 30px;
      font-weight: 400;
      font-size: 14px;
      line-height: 22px;
      color: var(--light-color);
      transition: all .3s ease-in-out;

      &:hover {
        background: var(--accent-color);
        color: var(--light-color);
      }

      @include mobile {
        width: 50px;
      }
    }

    &:hover {
      background: var(--text-color);
      border: 1px solid var(--text-color);
      color: var(--light-color);
    }
  }
}

.volume {
  margin-right: 10px;
  margin-bottom: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__name {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: var(--text-color);
  }

  &__list {
    display: flex;
  }

  &__item {

    &:not(:last-child) {
      margin-right: 16px;
    }

  }

  &__btn {
    width: 70px;
    height: 38px;
    border: 1px solid var(--element-color);
    border-radius: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--text-color);
    transition: all .3s ease-in-out;

    @include big-desktop {
      width: 55px;
    }

    &--active {
      background-color: var(--accent-color);
      border: 1px solid var(--accent-color);
      color: var(--light-color);
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
    }

    &:hover {
      background: var(--text-color);
      border: 1px solid var(--text-color);
      color: var(--light-color);
    }
  }
}

.params {
  margin-bottom: 24px;
  display: flex;

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
  }

  &__name {
    margin: 0;
    margin-bottom: 12px;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: var(--text-color);
  }

  &__value {
    padding: 0 35px;
    display: flex;
    align-items: center;
    min-height: 41px;
    border-left: 1px solid var(--element-color);
    border-top: 1px solid var(--shady-color);
    border-bottom: 1px solid var(--shady-color);
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--light-color);

    @include big-desktop {
      padding: 0 20px;
      font-size: 14px;
    }
  }
}

.countries {
  display: flex;

  @include high-desktop {
    flex-direction: column;
  }

  &__origin {
    @include high-desktop {
      margin-bottom: 24px;
    }
  }

  &__title {
    margin: 0;
    padding-bottom: 5px;
    border-bottom: 1px solid #000;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: var(--text-color);

    @include high-desktop {
      border: none;
    }
  }

  &__name {
    margin-right: 30px;
    padding: 13px 32px 3px 37px;
    width: 277px;
    display: flex;
    background-image: url(../img/ireland.png);
    background-size: 30px 30px;
    background-position: 0 10px;
    background-repeat: no-repeat;
    border-right: 1px solid var(--element-color);
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--light-color);

    @include high-desktop {
      border: none;
    }
  }
}

.market {
  width: 364px;

  @include big-desktop {
    width: 200px;
  }

  &__select {
    margin: 0;
    display: block;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: var(--light-color);
    padding: 8px 46px;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    border: 1px solid var(--element-color);
    border-radius: 6px;
    appearance: none;
    background-color: inherit;
    background-image: url(../img/arrow.svg);
    background-repeat: no-repeat, repeat;
    background-position: right 20px top 50%, 0 0;
    background-size: 12px 6px, 100%;

    &:hover {
      border-color: var(--accent-color);
    }

    &:focus {
      border-color: var(--accent-color);
      box-shadow: 0 0 1px 3px var(--accent-color);
      outline: none;
      background: var(--dark-color);
    }

    & option {
      font-weight: 400;
    }

    @include high-desktop {
      margin-top: 13px;
    }
  }
}
