.swiper {
  display: flex;

  @include mobile {
    flex-direction: column-reverse;
  }

  &__nav {
    overflow: hidden;
    width: 100px;
    margin-right: 16px;
    flex-shrink: 0;
    height: 526px;

    & .swiper-slide {
      background: var(--shady-color);
      border-radius: 8px;
      width: 100px;
      height: 100px;

      & img {
        max-height: 100%;
      }
    }

    @include high-tablet {
      width: 40%;
    }

    @include small-tablet {
      width: 20%;
    }

    @include mobile {
      width: 100%;
      height: auto;
    }
  }

  &__share {
    position: absolute;
    top: 15px;
    right: 22px;
    width: 16px;
    height: 21px;
    background-image: url(../img/share.png);
    background-repeat: no-repeat;
    background-position: center;
  }

  &__main {
    overflow: hidden;
    height: 526px;

    & .swiper-slide {

      & img {
        position: relative;
        max-height: 100%;
        background: #1C1C1E;
        border-radius: 8px;

        @include mobile {
          min-height: 380px;
        }
      }
    }

    @include mobile {
      margin-bottom: 5px;
      height: auto;
    }
  }

  &-button-next, &-button-prev {
    position: static;
    margin-top: 0;
    margin-left: 24px;
    width: 38px;
    height: 38px;
    background: transparent;
    border-radius: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 6px 12px;
    transition: all .3s ease-in-out;

    &::before, &::after {
      display: none;
    }
  }

  &-button-next {
    background-image: url(../img/right.png);
  }

  &-button-prev {
    background-image: url(../img/left.png);
  }

  &-button-next:hover, &-button-prev:hover {
    background: var(--accent-color);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 6px 12px;
  }

  &-button-next:hover {
    background-image: url(../img/right.png);
  }

  &-button-prev:hover {
    background-image: url(../img/left.png);
  }
}

.swiper2 {
  display: flex;
  overflow: hidden;

  & .swiper-slide {
    margin-right: 32px;
  }
}
