.card {
  margin-bottom: 40px;
  padding-bottom: 42px;
  display: flex;
  flex-direction: column;
  width: 100%;
  border-bottom: 1px solid var(--second-color);

  @include mobile {
    margin-bottom: 49px;
    padding-bottom: 49px;
  }

  &__info {
    margin-bottom: 9px;
    display: flex;
    justify-content: space-between;

    @include mobile {
      margin-bottom: -8px;
    }
  }

  &__client {
    display: flex;
  }

  &__photo {
    margin-right: 12px;

    @include mobile {
      margin-right: 14px;
      width: 45px;
    }
  }

  &__main {

    @include mobile {
      display: flex;
    }

  }

  &__name {
    margin-bottom: 5px;
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: var(--light-color);

    @include mobile {
      align-self: center;
    }
  }

  &__date {
    align-self: center;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: var(--text-color);
  }

  &__text {
    margin-left: 73px;

    @include mobile {
      margin-left: 60px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 7px;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: var(--light-color);

    @include mobile {
      line-height: 18px;
    }
  }

  &__btn {
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: var(--accent-color);
  }
}

.points {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;

  @include mobile {
    -webkit-line-clamp: 6;
  }
}

.rating2 {
  margin-left: 6px;
  display: inline-flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  width: 112px;
  height: 16px;

  @include mobile {
    display: none;
  }

  &__item {
    margin-right: 3px;
    font-size: 20px;
    color: var(--inactive-color);
    transition: all .3s ease-in-out;
  }

  &[data-total-value="1"] &__item:nth-child(n + 5),
  &[data-total-value="2"] &__item:nth-child(n + 4),
  &[data-total-value="3"] &__item:nth-child(n + 3),
  &[data-total-value="4"] &__item:nth-child(n + 2),
  &[data-total-value="5"] &__item:nth-child(n + 1) {
    color: var(--accent-color);
  }
}
