.header {
  padding: 20px 0;
  width: 100%;
  background-color: var(--dark-color);
  border-bottom: 1px solid var(--second-color);

  @include mobile {
    padding-top: 8px;
    padding-bottom: 0;
    border: none;
  }

  &__container {
    @include flex-all-sb;

    @include big-desktop {
      flex-wrap: wrap;
      justify-content: center;
    }

    @include high-tablet {
      justify-content: space-between;
    }
  }

  &__logo {
    margin-right: 50px;

    @include big-desktop {
      margin-right: 180px;
    }

    @include high-tablet {
      margin-right: 0;
    }
  }

  &__search {
    margin-right: 32px;
    display: flex;

    @include big-desktop {
      margin-top: 20px;
      margin-right: 0;
      order: 1;
    }

    @include mobile{
      margin-top: 32px;
      width: 100%;
    }
  }

  &__catalog {
    position: relative;
    margin-right: 20px;
    padding-left: 30px;
    width: 140px;
    height: 40px;
    background-color: var(--accent-color);
    border-radius: 10px;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: var(--light-color);
    transition: all .3s ease-in-out;

    @include mobile {
      display: none;
    }

    &:hover {
      background: var(--text-color);
    }

    &--line {
      position: absolute;
      left: 30px;
      top: 18px;
      border-radius: 100px;
      width: 17.5px;
      height: 2px;
      background-color: var(--light-color);
      transform: translateX(-50%) translateY(-50%);

      &:first-child {
        margin-top: -6px;
      }

      &:last-child {
        margin-top: 6px;
      }
    }
  }

  &__input {
    padding: 12px;
    width: 343px;
    height: 38px;
    background-color: var(--highlight-color);
    border-radius: 8px;
    border: none;
  }

  &__btns {
    margin-left: auto;

    @include high-tablet {
      display: none;
    }
  }

  &__star {
    margin-right: 27px;
    position: relative;

    &::after {
      content: '25';
      position: absolute;
      top: -7px;
      left: 17px;
      width: 24px;
      height: 18px;
      border: 1px solid var(--accent-color);
      border-radius: 100px;
      background-color: var(--accent-color);
      font-size: 12px;
      color: var(--light-color);
    }
  }
}
