.tooltip {
  margin-left: auto;

  &__btn {
    @include flex-all-center;

    border-radius: 100%;
    width: 16px;
    height: 16px;
    border: 1px solid var(--highlight-color);

    svg {
      width: 2px;
      height: 10px;
      fill: var(--accent-color);
    }
    &:focus {
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
      }
    }

    @include hover {
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &__txt {
    border-radius: 5px;
    padding: 15px 20px;
    box-shadow: 0px 2px 8px rgba(20, 16, 36, 0.1);
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: var(--dark-color);
    background: var(--light-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}
