.recomended {
  padding-top: 45px;
  padding-bottom: 46px;
  background-color: var(--dark-color);

  @include mobile {
    padding-top: 28px;
  }

  &__top {
    margin-bottom: 31px;
    display: flex;
    justify-content: space-between;
  }

  &__pagination {
    display: flex;
  }

  &__title {
    margin: 0;
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;
    color: var(--light-color);

    @include mobile {
      font-weight: 400;
      font-size: 20px;
    }
  }

  &__drink {
    min-height: 438px;
    padding: 11px 12px 11px 16px;
    display: flex;
    flex-direction: column;
    background-color: var(--shady-color);
    border-radius: 5px;
    transition: all .3s ease-in-out;

    &:hover {
      background-color: var(--dark-color);
    }
  }

  &__star {
    margin-bottom: 32px;
    align-self: flex-end;
    font-size: 20px;
    color: var(--accent-color);
  }

  &__img {
    margin-bottom: 6px;
  }

  &__about {
    margin-bottom: auto;
    display: flex;
    flex-direction: column;
  }

  &__brand {
    margin-bottom: 13px;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: var(--text-color);
  }

  &__name {
    margin-bottom: 10px;
    max-width: 85%;
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: var(--light-color);
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;

    @include high-tablet {
      max-width: 100%;
    }
  }

  &__descr {
    max-width: 85%;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: -1px;
    color: var(--text-color);
  }

  &__options {
    display: flex;
    align-items: center;
  }

  &__ml {
    padding: 3px 13px 3px 0px;
    border-right: 1px solid var(--text-color);
    font-weight: 400;
    font-size: 12px;
    color: var(--text-color);
  }

  &__btn {
    margin-left: auto;
    margin-right: 4px;
    font-weight: 400;
    font-size: 12px;
    width: 71px;
    height: 28px;
    background: var(--highlight-color);
    border-radius: 8px;
    color: var(--light-color);
    transition: all .3s ease-in-out;

    &:hover {
      background: var(--accent-color);
    }
  }
}

.rating4 {
  margin-left: 14px;
  display: inline-flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  width: 92px;
  height: 12px;

  &__item {
    margin-left: 3px;
    font-size: 16px;
    color: var(--inactive-color);
  }

  &[data-total-value="1"] &__item:nth-child(n + 5),
  &[data-total-value="2"] &__item:nth-child(n + 4),
  &[data-total-value="3"] &__item:nth-child(n + 3),
  &[data-total-value="4"] &__item:nth-child(n + 2),
  &[data-total-value="5"] &__item:nth-child(n + 1) {
    color: var(--accent-color);
  }
}
