.reviews {
  padding-top: 60px;
  background-color: var(--dark-color);

  @include mobile {
    padding-top: 22px;
  }

  &__container {
    display: flex;
    justify-content: space-between;

    @include high-tablet {
      flex-direction: column;
    }
  }

  &__left {
    margin-right: 20px;
    margin-left: 9px;
    max-width: 850px;

    @include high-tablet {
      margin: 0;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 38px;
    display: flex;
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;
    color: var(--light-color);

    @include mobile {
      margin-bottom: 46px;
      font-weight: 400;
      font-size: 18px;
    }
  }

  &__total {
    margin-top: 8px;
    margin-left: 8px;
    align-self: self-start;
    font-weight: 400;
    font-size: 12px;
    line-height: 20px;
    color: var(--text-color);
  }

  &__right {
    min-width: 388px;

    @include high-tablet {
      margin-top: 40px;
    }

    @include mobile {
      display: none;
    }
  }

  &__rating {
    margin-bottom: 22px;
    padding: 17px 15px 6px;
    background: var(--shady-color);
    border-radius: 8px;
  }

  &__bt {
    font-weight: 400;
    font-size: 16px;
    width: 134px;
    height: 52px;
    background: var(--highlight-color);
    border-radius: 5px;
    color: var(--light-color);
    transition: all .3s ease-in-out;

    &:hover {
      background: var(--accent-color);
    }

    @include mobile {
      width: 100%;
    }
  }

  &__btn {
    font-weight: 400;
    font-size: 12px;
    line-height: 58px;
    width: 100%;
    height: 44px;
    background: var(--accent-color);
    border-radius: 5px;
    color: var(--light-color);
    transition: all .3s ease-in-out;

    &:hover {
      background: var(--text-color);
    }
  }
}

.rating3 {
  margin-bottom: 19px;
  display: flex;
  justify-content: space-between;

  &__list {
    margin-left: 3px;
    display: inline-flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    width: 150px;
    height: 40px;
  }

  &__number {
    display: block;
    font-weight: 400;
    font-size: 18px;
    line-height: 40px;
    color: var(--light-color);
  }

  &__item {
    margin-left: 11px;
    font-size: 20px;
    color: var(--inactive-color);
    cursor: pointer;
    transition: all .3s ease-in-out;

    &:hover, &:hover ~ & {
      color: var(--accent-color);
    }
  }

  &[data-total-value="1"] &__item:nth-child(n + 5),
  &[data-total-value="2"] &__item:nth-child(n + 4),
  &[data-total-value="3"] &__item:nth-child(n + 3),
  &[data-total-value="4"] &__item:nth-child(n + 2),
  &[data-total-value="5"] &__item:nth-child(n + 1) {
    color: #AC8E68;
  }
}

.progress {
  margin-bottom: 16px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  color: var(--text-color);

  &__block {
    position: relative;
    background-color: var(--highlight-color);
    width: 260px;
    height: 22px;
    border-radius: 4px;
  }

  &__bar {
    position: absolute;
    top: 2px;
    left: 2px;
    width: 10%;
    height: 18px;
    background: var(--accent-color);
    border-radius: 4px;

    &--1 {
      width: 34%;
    }

    &--2 {
      width: 80%;
    }

    &--3 {
      width: 43%;
    }

    &--4 {
      width: 50%;
    }

    &--5 {
      width: 12%;
    }
  }
}
