.nav {

  @include mobile {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: var(--dark-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }

  &__list {
    @include flex-v-center;

    @include mobile {
      @include flex-all-center;
      flex-direction: column;
      width: 100%;
      height: 100%;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 40px;

      @include big-desktop {
        margin-right: 50px;
      }

      @include small-tablet {
        margin-right: 20px;
      }

      @include mobile {
       margin-right: 0;
       margin-bottom: 30px;
      }
    }
  }

  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--light-color);
    transition: all .3s ease-in-out;

    &:hover {
      color: var(--accent-color);
    }

    @include mobile {
      font-size: 30px;
     }
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
}
