.form {
  margin-bottom: 60px;
  display: flex;
  flex-direction: column;
  min-width: 319px;

  @include mobile {
    margin-bottom: 38px;
  }

  &__label {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: 16px;
    width: 100%;

    @include mobile {
      margin-bottom: 14px;
    }
  }

  &__input {
    border-radius: 8px;
    width: 100%;
    height: 47px;
    padding: 0 16px;
    font-weight: 400;
    font-size: 14px;
    line-height: 26px;
    color: var(--dark-color);

    &::placeholder {
      color: var(--text-color);
    }
  }

  &__btn {
    padding: 14px 30px;
    background: var(--accent-color);
    border-radius: 10px;
    font-weight: 400;
    font-size: 14px;
    line-height: 26px;
    color: var(--light-color);
    transition: all .3s ease-in-out;

    &:hover {
      background: var(--text-color);
    }
  }
}

.just-validate-error-label {
  margin-bottom: 3px;
  color: var(--accent-color)!important;
}
