:root {
  // base
  --font-family: "Inter", sans-serif;
  --content-width: 1360px;
  --container-offset: 40px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #FFFFFF;
  --accent-color: #AC8E68;
  --text-color: #747474;
  --element-color: #606063;
  --shady-color: #1C1C1E;
  // --#636366;
  --highlight-color: #2E2E30;
  --inactive-color: #9B9B9B;
  // #D9D9D9;
  --second-color: rgba(60, 60, 67, 0.35);
  // #000000;
  // #8B8B8B;
  --dark-color: #171717;
}
