.description {
  padding-top: 95px;
  background-color: var(--dark-color);

  @include small-tablet {
    padding-top: 40px;
  }

  &__container {
    @include flex-h-center;

    color: var(--light-color);

    @include high-tablet {
      flex-direction: column;
    }
  }

  &__left {
    margin-right: 32px;
    padding: 30px 40px;
    width: 50%;
    background: var(--shady-color);
    border-radius: 8px;

    @include high-tablet {
      margin-right: 0;
      margin-bottom: 22px;
      width: 100%;
    }

    @include small-tablet {
      padding: 20px;
    }

    @include mobile {
      padding: 10px 14px 16px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 32px;
    line-height: 40px;
    color: var(--light-color);

    @include mobile {
      margin-bottom: 5px;
      font-weight: 400;
      font-size: 18px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    margin-bottom: 33px;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: var(--light-color);
  }

  &__item {
    display: flex;
    justify-content: space-between;
    padding: 15.5px 0;
    border-bottom: 1px solid var(--second-color);
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: var(--light-color);

    @include mobile {
      padding: 13.5px 0;
    }

    &--name {
      margin-right: 40px;
    }

    &--value {
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
    }
  }

  &__btn {
    width: 197px;
    height: 54px;
    background: var(--highlight-color);
    border-radius: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--light-color);
    transition: all .3s ease-in-out;

    &:hover {
      background: var(--accent-color);
    }

    @include mobile {
      width: 100%;
    }
  }

  &__right {
    padding: 30px 40px;
    width: 50%;
    background: var(--shady-color);
    border-radius: 8px;

    @include high-tablet {
      width: 100%;
    }

    @include small-tablet {
      padding: 20px;
    }

    @include mobile {
      padding: 10px 14px 16px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: var(--light-color);

    &:not(:last-child) {
      margin-top: 30px;
      margin-bottom: 22px;

      @include mobile {
        margin-top: 22px;
        margin-bottom: 20px;
        letter-spacing: 0.4px;
      }
    }

    @include mobile {
      line-height: 18px;
    }
  }
}
