.footer {
  background: var(--shady-color);

  &__container {
    display: flex;
    flex-direction: column;
  }

  &__top {
    display: flex;

    @include small-tablet {
      flex-direction: column-reverse;
    }
  }

  &__left {
    min-height: 382px;
    padding: 38px 28px 38px 0;
    border-right: 1px solid var(--element-color);

    @include small-tablet {
      margin: 37px 0;
      min-height: auto;
      padding: 0;
      border: none;
    }

    @include mobile {
      margin: 30px 0;
    }
  }

  &__title {
    margin: 0;
    margin-top: auto;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 18px;
    line-height: 30px;
    color: var(--light-color);
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 26px;
    font-weight: 400;
    font-size: 14px;
    line-height: 22px;
    color: var(--text-color);

    @include high-tablet {
      margin-bottom: 16px;
    }

    @include mobile {
      margin-bottom: 20px;
    }

    &--follow {
      margin-bottom: 16px;

      @include mobile {
        margin-bottom: 15px;
      }
    }
  }

  &__right {
    padding: 40px 0 0 30px;
    display: flex;

    @include high-tablet {
      flex-direction: column;
    }

    @include small-tablet {
      margin-top: 37px;
      padding: 0;
    }

    @include mobile {
      margin-top: 22px;
    }
  }

  &__products {
    margin-right: 268px;

    @include high-desktop {
      margin-right: 150px;
    }

    @include high-tablet {
      margin-bottom: 24px;
    }

    @include small-tablet {
      margin: 0;
      width: 100%;
      padding-bottom: 37px;
      border-bottom: 1px solid var(--element-color);
    }

    @include mobile {
      padding-bottom: 31px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 180px;

    @include mobile {
      max-height: 130px;
    }
  }

  &__item {
    margin-bottom: 14px;
    min-width: 165px;

    &:nth-child(5), &:last-child {
      margin-bottom: 0;
    }

    @include mobile {
      margin-bottom: 8px;
      min-width: 80px;
    }
  }

  &__link {
    text-decoration: none;
    color: var(--light-color);
  }

  &__contacts {
    max-width: 280px;

    @include small-tablet {
      max-width: none;
      padding: 37px 0;
      border-bottom: 1px solid var(--element-color);
    }

    @include mobile {
      padding: 30px 0 55px;
    }
  }

  &__address {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: var(--light-color);
  }

  &__bottom {
    padding: 40px 0;
    border-top: 1px solid var(--element-color);

    @include mobile {
      padding: 37px 0;
    }

    &-container {
      display: flex;
      justify-content: space-between;

      @include mobile {
        flex-direction: column;
      }
    }
  }

  &__terms, &__copy {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: var(--light-color);

    @include mobile {
      font-size: 14px;
    }

    &--privacy {
      margin-left: 36px;

      @include mobile {
        margin-left: 22px;
      }
    }
  }

  &__terms {
    @include mobile {
      margin-bottom: 14px;
    }
  }
}
